.form label,
.header {
  width: 100%;
  max-width: 500px;
  color: #fff !important;
}
.form {
  width: 500px;
}

.form button {
  width: 100%;
  max-width: 500px;
}

.container {
  padding: 20px;
  display: flex !important;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #227037;
  color: #fff !important;
}

.note-contaier {
  margin-bottom: 3rem;
  font-size: 16px;
  width: 100%;
  text-align: left;
}

.note-contaier p {
  margin-bottom: 1rem;
}

.note {
  margin-top: 2rem;
  margin-bottom: 0;
}

.note-contaier ul {
  margin-top: 0;
}

.btn-group {
  width: 100%;
  max-width: 500px;
  margin-top: 3rem;
  display: flex;
  justify-content: space-evenly;
}

.link {
  width: 100%;
  max-width: 500px;
  padding: 1rem;
}

.link .btn {
  color: #fff !important;
}
.link .btn:hover {
  color: lightgreen !important;
}
